import { loadState, saveState } from "@seamless/common";
import ajaxBuilder from "@seamless/ajax";
import { functor } from "@seamless/functional";
import config from "utils";

const storeToken = (headers: Headers) => {
  functor(headers.get("authorization")).map((token) => {
    saveState(token, "token");
  });
};

const getToken = () => loadState("sessionid");

const ajax = ajaxBuilder({
  client: `${config.protocol}://${config.server}:${config.port}${config.service}`,
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  //@ts-ignore
  auth: getToken,
  onDeAuthentication: () => console.log("deauth"),
  onServerError: () => console.log("server error"),
  onNetworkError: () => console.log("on network error"),
  timeout: 10000,
  saveToken: storeToken,
});

export default ajax;
